.problem {
  color: #3b566e;
  background: #f7fafc; }
  .problem h1 {
    font-weight: 500;
    margin-bottom: 20px; }
  .problem h2 {
    margin-top: 20px;
    font-size: 24px;
    line-height: 40px; }
  .problem p {
    font-size: 18px;
    line-height: 34px;
    margin-top: 20px; }

footer {
  padding-top: 60px;
  background: #4745e4;
  color: #ffffff; }
  footer .title {
    color: #fff;
    font-size: 16px;
    margin: 0 0 25px;
    font-weight: 500;
    position: relative;
    padding: 0 0 15px;
    text-align: center; }
  footer .text,
  footer .text a {
    color: #e3e3e3;
    font-size: 12px;
    line-height: 24px; }
  footer .copyright p {
    text-align: center;
    margin-bottom: 0; }
    footer .copyright p a {
      color: #fff; }
  footer img {
    width: 200px;
    display: block;
    margin: 0 auto 40px; }
